
import CryptoJS from 'crypto-js'
    //加密
   function encrypt (word, keyStr){
        keyStr = keyStr ? keyStr : '825429b7a55bb1a9';
        var key  = CryptoJS.enc.Utf8.parse(keyStr);
        var srcs = CryptoJS.enc.Utf8.parse(word);
        var encrypted = CryptoJS.AES.encrypt(srcs, key, {iv:key,mode:CryptoJS.mode.CBC,padding: CryptoJS.pad.ZeroPadding});
        return encrypted.toString();
    }
    //解密
   function decrypt (word, keyStr){
        keyStr = keyStr ? keyStr : '825429b7a55bb1a9';
        var key  = CryptoJS.enc.Utf8.parse(keyStr);
        var decrypt = CryptoJS.AES.decrypt(word, key, {iv:key,mode:CryptoJS.mode.CBC,padding: CryptoJS.pad.ZeroPadding});
        return CryptoJS.enc.Utf8.stringify(decrypt).toString();
    }
    export  {encrypt , decrypt}

